// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import App from './App';
import router from './router';
import './assets/css/style.default.css';
import './assets/vendor/magnific-popup/magnific-popup.css';
import './assets/css/custom.css';
import './assets/vendor/nouislider/nouislider.css';
import './assets/vendor/jquery/jquery.min.js';
import './assets/vendor/bootstrap/js/bootstrap.bundle.min.js';
import './assets/vendor/magnific-popup/jquery.magnific-popup.min.js';
import './assets/vendor/smooth-scroll/smooth-scroll.polyfills.min.js';
import './assets/vendor/bootstrap-select/js/bootstrap-select.min.js';
import './assets/vendor/object-fit-images/ofi.min.js';
import './assets/js/theme.js';

Vue.config.productionTip = false;

new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
});



